@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --h4: Inter;

  /* font sizes */
  --h4-size: 24px;
  --text-s-regular-size: 14px;
  --text-m-semi-bold-size: 15px;
  --body-midd-size: 16px;
  --h3-size: 38px;

  /* Colors */
  --neutral-light-4: #fff;
  --gray-50: #5f6d7e;
  --gray-500: #2e3646;
  --neutral-100: #f8f9fb;
  --neutral-1: #07090a;
  --neutral-light-3: #f7f7fc;
  --neutral-2: #4e4775;
  --color-lightsteelblue: #a0a3bd;

  /* Gaps */
  --gap-5xs: 8px;
  --gap-7xs: 6px;
  --gap-base: 16px;
  --gap-5xl: 24px;

  /* Paddings */
  --padding-5xl: 24px;
  --padding-45xl: 64px;
  --padding-5xs: 8px;
  --padding-lg: 18px;
  --padding-13xl: 32px;

  /* Border radiuses */
  --br-3xs: 10px;
  --br-5xs: 8px;
}
