.normal {
  position: relative;
  line-height: 38px;
}
.property1normal {
  border-radius: var(--br-3xs);
  background: linear-gradient(0deg, #6a35f5, #7e49ff);
  box-shadow: 0 4px 8px rgba(106, 53, 245, 0.3);
  display: flex;
  flex-direction: row;
  padding: var(--padding-5xl) var(--padding-45xl);
  align-items: flex-start;
  justify-content: flex-start;
  text-align: center;
  font-size: var(--h4-size);
  color: var(--neutral-light-4);
  font-family: var(--h4);
}
